.gpt3__features{
    width: 100%;
    display: flex;
    justify-content: space-between;
    column-gap: 5rem;
    row-gap: 3rem;
}

.gpt3__features-heading{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
}

.gpt3__features-heading h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
}

.gpt3__features-heading p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: #FF8A71;
    margin-top: 2rem;
}

.gpt3__features-container{
    flex: 1.5;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    gap: 20px !important;
}

@media screen and (max-width: 990px){
    .gpt3__features{
        flex-direction: column;       
    }
}

@media screen and (max-width: 550px){
    .gpt3__features-heading h1{
        font-size: 28px;
        line-height: 38px;
    }
    .gpt3__features-container .gpt3__features-container__feature-title {
        max-width: unset;
        margin-right: 0;
    }
}