.gpt3__navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem ;
}

.gpt3__navbar-links{
    flex: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.gpt3__navbar-links_logo{
    margin-right: 2rem;
}

.gpt3__navbar-links_logo img{
    width: 62px;
    height: 16px;
    cursor: pointer;
}

.gpt3__navbar-links_container{
    display: flex;
    flex-direction: row;
}

.gpt3__navbar-sign{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-left: auto;
}

.gpt3__navbar-links_container p,
.gpt3__navbar-sign p,
.gpt3__navbar-menu_container p{
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
    position: relative;
}

.gpt3__navbar-links_container p::before,
.gpt3__navbar-sign p::before{
    content: '';
    display: block;
    position: absolute;
    top: calc(100% + 10px);
    left: 50%;
    transform: translateX(-50%);
    height: 3px;
    width: 0;
    background-image: var(--gradient-bar);
    transition: all 0.3s ease-in-out;
}

.gpt3__navbar-links_container p:hover::before,
.gpt3__navbar-sign p:hover::before{
    width: 100%;
}

.gpt3__navbar-sign button,
.gpt3__navbar-menu_container button{
    padding: 0.5rem 1rem;
    background: #FF4820;
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border-radius: 5px;
    border: 0;
    outline: none;
    cursor: pointer;
}

.gpt3__navbar-menu{
    margin-left: 1rem;
    display: none;
    position: relative;
}

.gpt3__navbar-menu_container{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: right;
    background: var(--color-footer);
    padding: 1rem 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 200px;
    border-radius: 5px;
    box-shadow: 0 0 5px rgba(0,0,0,0.2);
}

.gpt3__navbar-menu_container p{
    margin: 1rem 0;
}

.gpt3__navbar-menu_container-links-sign{
    display: none;
}

@media screen and (max-width: 1050px){
    .gpt3__navbar-links_container{
        display: none;
    }
    .gpt3__navbar-menu{
        display: flex;
    }
}

@media screen and (max-width: 700px){
    .gpt3__navbar{
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px){
    .gpt3__navbar{
        padding: 2rem 1rem;
    }
    .gpt3__navbar-sign{
        display: none;
    }
    .gpt3__navbar-menu_container{
        top: 20px;
    }
    .gpt3__navbar-menu_container-links-sign{
        display: block;
        padding-bottom: 1rem;
    }
    .gpt3__navbar-links_container p,
    .gpt3__navbar-sign p,
    .gpt3__navbar-menu_container p{
        font-size: 14px;
        line-height: 24px;
    }
    .gpt3__navbar-sign button,
    .gpt3__navbar-menu_container button{
        padding: 0.5rem 1rem;
        font-size: 14px;
        line-height: 24px;
    }
    .gpt3__navbar-menu_container p{
        margin: 0.6rem 0;
    }
}